.hero {
    background: #F4F3F0;
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    padding-top: 80px;
    overflow: hidden;
}

.hero::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 400px;
    height: 400px;
    background: radial-gradient(circle at top right, rgba(58, 95, 86, 0.55) 0%, rgba(58, 95, 86, 0) 70%);
    pointer-events: none;
    z-index: 3;
}

.hero::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 350px;
    height: 350px;
    background: radial-gradient(circle at bottom left, rgba(184, 125, 88, 0.35) 0%, rgba(184, 125, 88, 0) 65%);
    pointer-events: none;
    z-index: 3;
}

.hero .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
    position: relative;
    z-index: 2;
}

.hero-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    align-items: center;
}

.hero-text h1 {
    font-size: 3.1rem;
    font-weight: 400;
    color: #3A5F56;
    margin-bottom: 1.5rem;
    line-height: 1.2;
    font-family: 'Source Serif Pro', serif;
}

.hero-text p {
    font-size: 1rem;
    color: #292C32;
    margin-bottom: 2rem;
    line-height: 1.6;
    font-family: 'Roboto', sans-serif;
}

.cta-button {
    background: #3A5F56;
    color: white;
    border: none;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1rem;
    font-weight: 400;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(58, 95, 86, 0.3);
}

.cta-button:hover {
    background: #2d4a42;
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(58, 95, 86, 0.4);
}

.hero-image {
    position: relative;
    z-index: 2;
}

.hero-image img {
    width: 100%;
    height: auto;
    border-radius: 24px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
    .hero-content {
        grid-template-columns: 1fr;
        gap: 2rem;
        text-align: center;
    }

    .hero-text h1 {
        font-size: 2.5rem;
    }

    .hero::before {
        right: 50%;
        transform: translateX(50%);
        width: 300px;
        height: 300px;
    }

    .hero::after {
        left: 50%;
        transform: translateX(-50%);
        width: 280px;
        height: 280px;
    }
}